.App {
  width: 100%;
  max-width: 68rem;
  margin: 0 auto;
}

.App__heading {
  text-align: center;
  padding: 3rem 0;
  font-size: 3.5rem;
  font-weight: 400;
}

.App__input-and-buttons {
  position: relative;
  width: 100%;
  height: 5rem;
  border: .5rem solid var(--light-grey-color);
}

.App__button-open {
  position: absolute;
  width: 4rem;
  height: 4rem;
  top: 0;
  left: 0;
}

.App__button-clear {
  position: absolute;
  width: 3rem;
  height: 3rem;
  top: .5rem;
  right: 0;
}
